import Link from "next/link";

class Button extends React.Component {
  state = {
    active: false
  };

  render() {
    const { children, href } = this.props;

    return (
      <div className="container">
        <Link href={href}>
          <a onClick={() => this.setState({ active: !this.state.active })}>
            <p>{children}</p>
          </a>
        </Link>
        <style jsx>
          {`
            p {
              font-size: 26px;
              color: #9a4c8e;
            }
          `}
        </style>
      </div>
    );
  }
}

export default Button;



